export default {
    name: "grid-control",

    data() {
        return {
            selRowsIndex: -1,
            selRow: {}
        };
    },

    props: {
        showRowNum: {
            type: Boolean,
            required: false,
            default: true
        },

        width: {
            type: String,
            required: false,
            default: "100%"
        },
        height: {
            type: String,
            required: false,
            default: "200px"
        },

        data: {
            type: Array,
            required: false
        },
        options: {
            type: Object,
            required: true
        },

        deadAreaColor: {
            type: String,
            required: false,
            default: "#CCC"
        },
        includeFooter: {
            type: Boolean,
            required: false,
            default: false
        },
        syncHeaderScroll: {
            type: Boolean,
            required: false,
            default: true
        },
        syncFooterScroll: {
            type: Boolean,
            required: false,
            default: true
        },

        scrollHorizontal: {
            type: Boolean,
            required: false,
            default: true
        },
        scrollVertical: {
            type: Boolean,
            required: false,
            default: true
        },

        wordWrap: {
            type: Boolean,
            required: false,
            default: false
        }
    },

    computed: {
        getSelRowIndex() {
            return this.selRowsIndex;
        },
        tableStyle() {
            return `background-color: ${this.deadAreaColor};
                width: ${this.width};
                height: ${this.height};`;
        },
        stubScrollbarStyle() {
            return `background-color: ${this.deadAreaColor};
          scrollbar-base-color: ${this.deadAreaColor};
          scrollbar-face-color: ${this.deadAreaColor};
          scrollbar-highlight-color: ${this.deadAreaColor};
          scrollbar-track-color: ${this.deadAreaColor};
          scrollbar-arrow-color: ${this.deadAreaColor};
          scrollbar-shadow-color: ${this.deadAreaColor};
          scrollbar-darkshadow-color: ${this.deadAreaColor};`;
        }
    },
    watch: {
        deadAreaColor() {
            this.setColors();
        }
    },
    mounted: function () {
        this.setColors();
        this.updateSyncedScroll();
    },
    methods: {
        updateSyncedScroll() {
            const b = this.$refs.tbody;
            const l = b.scrollLeft;
            if (this.scrollHorizontal) {
                if (this.syncHeaderScroll) {
                    const h = this.$refs.thead;
                    if (h.scrollLeft !== l) {
                        h.scrollLeft = l;
                    }
                }
                if (this.includeFooter && this.syncFooterScroll) {
                    const f = this.$refs.tfoot;
                    if (f.scrollLeft !== l) {
                        f.scrollLeft = l;
                    }
                }
            }
            this.$emit("scroll", b.scrollTop, l, b.scrollHeight, b.scrollWidth);
        },
        setColors() {
            const s = this.$refs.table.style;
            s.setProperty("--dead-area-color", this.deadAreaColor);
        },

        onRowSelectClick(m, i) {
            this.selRow = m;
            if (this.selRowsIndex == i) {
                this.selRowsIndex = -1;
            } else {
                this.selRowsIndex = i;
            }

            this.$emit("onRowSelect", m, i);
        }
    }
};


