import MainMenu from "./../components/MainMenu";
import Carousel from "./../components/Carousel";

window.$ = window.jQuery = require("jquery"); // <-- main, not 'slim'
window.Popper = require("popper.js");
import "bootstrap";

// require("../assets/js/vendors/jquery-ui.min.js")
// require("../assets/js/input-mask/jquery.inputmask.js")
// require("../assets/js/input-mask/jquery.inputmask.date.extensions.js")
// require("../assets/js/input-mask/jquery.inputmask.numeric.extensions.js")


export default {
  name: "App",

  data: function () {
    return {
      loadingSpinner: false,
      loadingTest: "Юкланмоқда",
    }
  },

  
  mounted: function () {
    this.$root.$on('LOADER', (item) => {
      this.loadingSpinner = item;
    })
  },

  components: {
    MainMenu,
    Carousel
  }
};



