import UserWebConference from '../../components/UserWebConference'
import SignalRConnection from '../../assets/js/SignalRConnection';
import adapter from 'webrtc-adapter';

export default {
  data: function () {
    return {
      connection: null,
      rtm: null,


      confID: '122222',
      videosList: []
    }
  },

  components: {
    UserWebConference
  },

  methods: {

    openRoom() {
      this.rtm.open(this.confID, function () {});
    },

    joinRoom() {
      this.rtm.join(this.confID);
    },


    initSocket() {

      const app = this;
      this.rtm = new RTCMultiConnection();
      this.rtm.setCustomSocketHandler(SignalRConnection);
      this.rtm.session = {
        audio: true,
        video: true
      };

      this.rtm.sdpConstraints.mandatory = {
        OfferToReceiveAudio: true,
        OfferToReceiveVideo: true
      };

      this.rtm.onstream = (event) => {
        //TODO Tekshrish kk
        //event.mediaElement.src = event.mediaElement.srcObject = null;
        
        app.videosList.push({
          id: event.streamid,
          src: URL.createObjectURL(event.stream),
          muted: event.type === 'local'
        });
      };

      this.rtm.onstreamended = (event) => {
        var newList = [];
        app.videosList.forEach(function (item) {
          if (item.id !== event.streamid) {
            newList.push(item);
          }
        });
        app.videosList = newList;
      };


      this.rtm.onMediaError = (e) => {
        if (e.message === 'Concurrent mic process limit.') {
          if (DetectRTC.audioInputDevices.length <= 1) {
            alert('Please select external microphone. Check github issue number 483.');
            return;
          }

          var secondaryMic = DetectRTC.audioInputDevices[1].deviceId;
          connection.mediaConstraints.audio = {
            deviceId: secondaryMic
          };

          connection.join(connection.sessionid);
        }
      };
    },
  },

  created() {
    window.enableAdapter = true; // enable adapter.js 
  },

  mounted() {
    this.initSocket();
  }
}



