import Vue from 'vue'
import RTCPeerConnection from 'webrtc-adapter'

export default {
  data() {
    return {
      connection: {},
      room: 'foo',
      vdiLocal: {},
      vdiRemote: {},
      isInitiator: false,
      isStarted: false,
      isChannelReady: false,
      turnReady: false,
      localStream: {
        type: Object
      },
      remoteStream: {
        type: Object
      },
      pc: {
        type: Object
      },
      pc_config: {
        'iceServers': [{
          'url': 'stun:stun.l.google.com:19302'
        }]
      },
      sdpConstraints: {
        'mandatory': {
          'OfferToReceiveAudio': true,
          'OfferToReceiveVideo': true
        }
      },
      constraints: {
        video: true
      }
    }
  },

  created: function () {
    this.connection = new this.$signalR.HubConnectionBuilder()
      .withUrl(this.$vars.SERVER_URL + "/mescenter")
      .configureLogging(this.$signalR.LogLevel.Error)
      .build();
    this.connection.start()
      .then(() => {

        if (this.room !== '') {
          let d = {};
          d.mesType = 'join';
          d.data = this.room;
          this.sendMessage(d);
        }

        navigator.getUserMedia(this.constraints, this.handleUserMedia, this.handleUserMediaError)

        if (location.hostname != "localhost") {
          this.requestTurn('https://computeengineondemand.appspot.com/turn?username=41784574&key=4080218913');
        }
      })
      .catch(err => console.error(err.toString()));
  },

  mounted: function () {
    let _t = this;
    this.connection.on('SendOther', mes => {

      switch (mes.mesType) {
        case 'created':
          this.isInitiator = true;
          console.log('Created room ' + mes.data + ' isInitiator(socket): ' + this.isInitiator)
          break;

        case 'full':
          console.log('Room ' + mes.data + ' is full');
          break;

        case 'join':
          this.isChannelReady = true;
          console.log('Join room ' + mes.data);
          break;

        case 'joined':
          console.log('This peer has joined room ' + mes.data);
          this.isChannelReady = true;
          break;

        case 'log':
          console.log(mes.data);
          break;

        case 'got user media':
          this.maybeStart()
          break;

        case 'message':
          let message = mes.data;

          if (message.type === 'offer') {
            if (!this.isInitiator && !_this.isStarted) {
              this.maybeStart();
            }
            this.pc.setRemoteDescription(new RTCSessionDescription(message));
            this.doAnswer();

          } else if (message.type === 'answer' && this.isStarted) {
            this.pc.setRemoteDescription(new RTCSessionDescription(message));
          } else if (message.type === 'candidate' && this.isStarted) {
            let candidate = new RTCIceCandidate({
              sdpMLineIndex: message.label,
              candidate: message.candidate
            });
            this.pc.addIceCandidate(candidate);
          }
          break;

        case 'bye':
          if (this.isStarted)
            this.handleRemoteHangup();
          break;
      }

    })

    window.onbeforeunload = function (e) {
      _t.sendMessage({
        mesType: 'bye'
      })
      console.log('bye');
    }
  },

  methods: {

    sendMessage: function (message) {
      var json = JSON.stringify(message);
      this.connection.invoke("SendOther", json).catch(err => console.error(err.toString()));
      console.log('Client sending message: ', message);
    },

    maybeStart: function () {
      if (!this.isStarted && this.localStream.active === true && this.isChannelReady) {
        this.createPeerConnection();
        this.pc.addStream(this.localStream);
        this.isStarted = true;
        console.log('isInitiator', this.isInitiator);
        if (this.isInitiator) {
          this.doCall();
        }
      }
    },

    handleUserMedia: function (stream) {
      let mediaStream = stream;
      this.vdiLocal = document.getElementById('localVideo');

      if (typeof (this.vdiLocal.srcObject) !== 'undefined') {
        this.vdiLocal.srcObject = mediaStream;
      } else {
        this.vdiLocal.src = URL.createObjectURL(mediaStream);
      }

      this.localStream = stream;
      this.sendMessage({
        mesType: 'got user media'
      });
      console.log("isInitiator(handleUserMedia): ", this.isInitiator);
      if (this.isInitiator) {
        this.maybeStart();
      }
    },

    handleUserMediaError: function (error) {
      console.log('getUserMedia error: ', error);
    },

    createPeerConnection: function () {
      try {
        this.pc = new RTCPeerConnection(null);
        this.pc.onicecandidate = this.handleIceCandidate;
        this.pc.onaddstream = this.handleRemoteStreamAdded;
        this.pc.onremovestream = this.handleRemoteStreamRemoved;
        console.log('Created RTCPeerConnnection');
      } catch (e) {
        console.log('Failed to create PeerConnection, exception: ' + e.message);
        alert('Cannot create RTCPeerConnection object.');
        return;
      }
    },

    handleIceCandidate(event) {
      console.log('handleIceCandidate event: ', event);
      if (event.candidate) {
        let d = {};
        d.mesType = 'message';
        d.data = {
          type: 'candidate',
          label: event.candidate.sdpMLineIndex,
          id: event.candidate.sdpMid,
          candidate: event.candidate.candidate,
        };
        this.sendMessage(d);

      } else {
        console.log('End of candidates.');
      }
    },

    handleCreateOfferError(event) {
      console.log('createOffer() error: ', event);
    },

    doCall() {
      console.log('Sending offer to peer');
      this.pc.createOffer(this.setLocalAndSendMessage, this.handleCreateOfferError);
    },

    doAnswer() {
      console.log('Sending answer to peer.');
      this.pc.createAnswer(this.setLocalAndSendMessage, null, this.sdpConstraints);
    },

    setLocalAndSendMessage(sessionDescription) {
      sessionDescription.sdp = this.preferOpus(sessionDescription.sdp);
      this.pc.setLocalDescription(sessionDescription);

      let d = {
        mesType: 'message',
        data: sessionDescription
      };
      this.sendMessage(d);

      console.log('setLocalAndSendMessage sending message', sessionDescription);
    },

    requestTurn(turn_url) {
      let turnExists = false;
      for (let i in this.pc_config.iceServers) {
        if (this.pc_config.iceServers[i].url.substr(0, 5) === 'turn:') {
          turnExists = true;
          this.turnReady = true;
          break;
        }
      }
      if (!turnExists) {
        console.log('Getting TURN server from ', turn_url);
        // No TURN server. Get one from computeengineondemand.appspot.com:
        let xhr = new XMLHttpRequest();
        xhr.onreadystatechange = function () {
          if (xhr.readyState === 4 && xhr.status === 200) {
            let turnServer = JSON.parse(xhr.responseText);
            console.log('Got TURN server: ', turnServer);
            this.pc_config.iceServers.push({
              'url': 'turn:' + turnServer.username + '@' + turnServer.turn,
              'credential': turnServer.password
            });
            this.turnReady = true;
          }
        };
        xhr.open('GET', turn_url, true);
        xhr.send();
      }
    },

    handleRemoteStreamAdded(event) {

      this.remoteStream = event.stream;
      this.vdiLocal = document.getElementById('localVideo');

      if (typeof (this.vdiRemote.srcObject) !== 'undefined') {
        this.vdiRemote.srcObject = this.remoteStream;
      } else {
        this.vdiRemote.src = URL.createObjectURL(this.remoteStream);
      }

      console.log('Remote stream added.');
    },

    handleRemoteStreamRemoved(event) {
      console.log('Remote stream removed. Event: ', event);
    },

    hangup() {
      console.log('Hanging up.');
      this.stop();
      this.sendMessage('bye');
    },

    handleRemoteHangup() {
      //  console.log('Session terminated.');
      // stop();
      // isInitiator = false;
    },

    stop() {
      this.isStarted = false;
      // isAudioMuted = false;
      // isVideoMuted = false;
      this.pc.close();
      this.pc = null;
    },

    preferOpus(sdp) {
      // Set Opus as the default audio codec if it's present.
      let sdpLines = sdp.split('\r\n');
      let mLineIndex;
      // Search for m line.
      for (let i = 0; i < sdpLines.length; i++) {
        if (sdpLines[i].search('m=audio') !== -1) {
          mLineIndex = i;
          break;
        }
      }
      if (mLineIndex === null) {
        return sdp;
      }

      // If Opus is available, set it as the default in m line.
      for (let i = 0; i < sdpLines.length; i++) {
        if (sdpLines[i].search('opus/48000') !== -1) {
          let opusPayload = this.extractSdp(sdpLines[i], /:(\d+) opus\/48000/i);
          if (opusPayload) {
            sdpLines[mLineIndex] = this.setDefaultCodec(sdpLines[mLineIndex], opusPayload);
          }
          break;
        }
      }

      // Remove CN in m line and sdp.
      sdpLines = this.removeCN(sdpLines, mLineIndex);

      sdp = sdpLines.join('\r\n');
      return sdp;
    },

    extractSdp(sdpLine, pattern) {
      let result = sdpLine.match(pattern);
      return result && result.length === 2 ? result[1] : null;
    },

    setDefaultCodec(mLine, payload) {
      // Set the selected codec to the first in m line.
      let elements = mLine.split(' ');
      let newLine = [];
      let index = 0;
      for (let i = 0; i < elements.length; i++) {
        if (index === 3) { // Format of media starts from the fourth.
          newLine[index++] = payload; // Put target payload to the first.
        }
        if (elements[i] !== payload) {
          newLine[index++] = elements[i];
        }
      }
      return newLine.join(' ');
    },

    removeCN(sdpLines, mLineIndex) {
      // Strip CN from sdp before CN constraints is ready.
      console.log(sdpLines[mLineIndex]);
      let mLineElements = sdpLines[mLineIndex].split(' ');
      // Scan from end for the convenience of removing an item.
      for (let i = sdpLines.length - 1; i >= 0; i--) {
        let payload = this.extractSdp(sdpLines[i], /a=rtpmap:(\d+) CN\/\d+/i);
        if (payload) {
          let cnPos = mLineElements.indexOf(payload);
          if (cnPos !== -1) {
            // Remove CN payload from m line.
            mLineElements.splice(cnPos, 1);
          }
          // Remove CN line in sdp
          sdpLines.splice(i, 1);
        }
      }

      sdpLines[mLineIndex] = mLineElements.join(' ');
      return sdpLines;
    }
  }
}



