import Vue from "vue";

export default {

  name: 'Register',
  data() {
    return {
      application: {}
    }    
  },
  methods: {
    
    doRegister() {
      this.$dataService.post('api/course/student/register', this.application).then((response) => {         
          this.$router.push("/completed");
        },
        error => {
          console.log(error);
        });
    }
  }
}


