import Vue from "vue";
import {Config} from "./config.js";


class Rdm {
  constructor() {
    this.type = "Person";
    this.ServerUrl = Config.SERVER_URL;
    this.cache = new Map();
  }

   post = function (url, param) {
    if (param) {
      return Vue.http.post(this.ServerUrl + url, param);
    } else {
      return Vue.http.post(this.ServerUrl + url)
    }
  }

  get = function (url, param) {
    if (param) {
      return Vue.http.get(this.ServerUrl + url + '/' + param);
    } else {
      return Vue.http.get(this.ServerUrl + url)
    }
  }

  delete = function (url) {
    if (param) {
      return Vue.http.delete(this.ServerUrl + url + '/' + param);
    } else {
      return Vue.http.delete(this.ServerUrl + url)
    }
  }

  put = function (url, parm) {
    if (param) {
      return Vue.http.put(this.ServerUrl + url, param);
    } else {
      return Vue.http.put(this.ServerUrl + url)
    }
  }


  getUser = function () {
    let u = sessionStorage.getItem('user');
    if (u) {
      return JSON.parse(u);
    }
  }

  setUser = function (u) {
    let s = JSON.stringify(u);
    sessionStorage.setItem('user', s);
  }
};

export {
  Rdm
};



