import Vue from "vue";

export default {

  name: 'Login',
  data() {
    return {
      p: {},
      error_msg:''
    }    
    
  },
  methods: {
    
    login() {
      this.$dataService.post('api/auth/token', this.p).then((response) => {
          let u = response.body;
          this.$dataService.setUser(u);

          if (u.role === "Student") {
            this.$router.push("/studentui");
          }

        },
        error => {
          this.error_msg = error.body.error;
          console.log(error.body.error);
        });
    }
  }
}



