import UserTeacherCast from '../../components/UserTeacherCast'
import WhiteBoardBtnsControl from '../../components/WhiteBoardBtnsControl'
import { Canvas } from "fabric"


export default {
  data: function () {
    return {
      connection: null,
      ctx: {}
    }
  },

  props: [],

  components: {
    UserTeacherCast,
    WhiteBoardBtnsControl
  },

  methods: {
    init() {

      var $canvasDiv = $('#canvasdiv');
      var canvas = document.getElementById("c");
      canvas.height = $canvasDiv.innerHeight();
      canvas.width = $canvasDiv.innerWidth();

      this.ctx = new fabric.Canvas('c', {
        isDrawingMode: true
      });
      this.ctx.freeDrawingBrush.color = '#343a40';
      this.ctx.freeDrawingBrush.width = 5;

      if (this.$route.params.id=='admin')
        this.ctx.on('object:added', this.objAdd);
        this.ctx.on('object:modified', this.objAdd);
        this.ctx.on('object:moving', this.objAdd);
        this.ctx.on('object:removed', this.objAdd);
        this.ctx.on('object:rotating', this.objAdd);
        this.ctx.on('object:scaling', this.objAdd);
    },

    objAdd(e) {

      var json = JSON.stringify(this.ctx);
      this.connection.invoke("SendOther", json).catch(err => console.error(err.toString()));
      console.log(json.length);

    },

    test() {

      this.connection.invoke("SendAll", "message").catch(err => console.error(err.toString()));
    }
  },

  created() {

    this.connection = new this.$signalR.HubConnectionBuilder()
      .withUrl(this.$vars.SERVER_URL + "/mescenter")
      .configureLogging(this.$signalR.LogLevel.Error)
      .build();
  },

  mounted() {
    
    this.init();

    this.connection.start();

    this.connection.on('SendOther', data => {
      console.log(data);

      this.ctx.loadFromJSON(data);
    });
  }
}



