import {
  Canvas
} from "fabric";

export default {
  name: 'white-board-btns-control',

  data() {
    return {
      isShowed: false,
      curColor: '#343a40',
    };
  },

  props: {
    context: {},
  },

  computed: {

  },

  mounted: function () {
    this.isShowed = true;
  },

  methods: {
    onClick: function (i) {
      switch (i) {
        case 0:
          this.onColor();
          break;
        case 1:
          this.onNew();
          break;
        case 2:
          this.onSelectMode();
          break;
        case 3:
          this.onDrawMode();
          break;
        case 4:
          this.onText();
          break;
          case 5:
          this.onLine();
          break;          
        case 6:
          this.onSquare();
          break;
        case 7:
          this.onCircle();
          break;
        case 8:
          this.onImage();
          break;
        case 9:
          this.onDeleteSelected();
          break;
        case 10:
          this.onSaveToImage();
          break;
      }
    },
    getColor: function () {
      if (this.isShowed) {
        this.curColor = document.getElementById("colorDialogID").value;
        this.context.freeDrawingBrush.color = this.curColor;
      }
    },
    onColor: function () {
      document.getElementById("colorDialogID").focus();
      document.getElementById("colorDialogID").value = "#FFCC00"; //Set the default color  NOTE: Remove the line to get the default of #000000
      document.getElementById("colorDialogID").click();
    },
    onNew: function () {
      this.context.clear()
    },
    onSelectMode: function () {
      this.context.isDrawingMode = false;
    },
    onDrawMode: function () {
      this.context.isDrawingMode = true;
    },
    onText: function () {
      var textbox = new fabric.Textbox('Lorum ipsum dolor sit amet', {
        left: 50,
        top: 50,
        width: 150,
        fontSize: 20
      });
      this.context.add(textbox).setActiveObject(textbox);
    },
    onLine: function () {
      var line = new fabric.Line([150, 50, 150, 0], {
        fill: this.curColor,
        stroke: this.curColor,
        strokeWidth: 1,
        hasControls: true,
        hasRotatingPoint: true,
        padding: 10,
        left: 80,
        top: 50,
        scaleX: 3,
        scaleY: 3
    });
      this.context.add(line);
      this.context.isDrawingMode = false;
    },
    onSquare: function () {
      var rect = new fabric.Rect({
        top: 100,
        left: 100,
        width: 60,
        height: 70,
        stroke: '#343a40',
        fill: this.curColor
      });
      this.context.add(rect);
      this.context.isDrawingMode = false;
    },
    onCircle: function () {
      var circle = new fabric.Circle({
        left: 100,
        top: 100,
        width: 150,
        radius: 100,
        fill: this.curColor,
        stroke: '#343a40',
        scaleY: 0.5,
        originX: 'center',
        originY: 'center'
      });
      this.context.add(circle);
      this.context.isDrawingMode = false;
    },
    onImage: function () {},
    onDeleteSelected: function () {
        var activeObject = this.context.getActiveObject();
         if (activeObject) this.context.remove(activeObject);
         this.context.renderAll();
    },
    onSaveToImage: function () {},
  }
};



