import Vue from 'vue'
import Router from 'vue-router'
import FromPoisk from '../Froms/Poisk'
import TestControl from '../components/TestControl'
import Login from '../Froms/Login'
import StudentRegister from '../Froms/StudentRegister'
import StudentUI from '../Froms/StudentUI'
import RegistrationCompleted from '../Froms/RegistrationCompleted'
import ForgotPassword from '../Froms/ForgotPassword'
import FrmKurs from '../Froms/Kurs'
import FrmWebConference from '../Froms/WebConference'
import FrmTeacherChat from '../Froms/TeacherChat'
import FrmAbout from '../Froms/About'
import FrmUserProfile from '../Froms/UserProfile'
import WebRtc from '../Froms/WebRtc'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [ 
    { path:'/',  component: FromPoisk },
    { path:'/t', component: TestControl },
    { path:'/about', component: FrmAbout },
      
    { path:'/login',   component: Login },
    { path:'/register', component: StudentRegister },
    { path:'/forgot',  component: ForgotPassword },    
    { path:'/completed',  component: RegistrationCompleted },    
    { path:'/webrtc',   component: WebRtc },
    { path:'/webconference',   component: FrmWebConference },
    { path:'/webconference/:id',   component: FrmWebConference },
    { path:'/chat_teacher',     component: FrmTeacherChat },
    { path:'/chat_teacher/:id', component: FrmTeacherChat },
    { path:'/userprofile',      component: FrmUserProfile },
    { path:'/studentui',      component: StudentUI, 
      children: [
        { path:'direction', component: FrmKurs }
      ]
    }
  ]
})



